.people {
  --columns: 2;
  color: #2f4f4f;
  box-sizing: border-box;
  grid-template-columns: repeat(var(--columns), 220px);
  justify-content: center;
  gap: 30px 10px;
  padding: 30px 35px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 100%;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    --columns: 4;
  }
}

.person {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  padding: 10px 10px 16px;
}

.person__photo {
  position: relative;
}

.person__photo > img {
  border-radius: 50%;
  width: 100%;
}

.person__photo-rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  margin-right: 10px;
  font-size: 16px;
  line-height: 200%;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.person__details {
  text-align: center;
  margin-top: 8px;
}

.person__extra {
  color: #000;
  text-align: center;
  background: #eee;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  width: 100%;
  padding-top: 9px;
  padding-bottom: 16px;
  font-size: 18px;
  font-weight: 400;
  display: none;
  position: absolute;
  left: 0;
}

.person__extra-sport:after {
  content: ",";
}

.person__extra-pay:before {
  content: "$";
}

.person__extra-pay:after {
  content: "M";
}

.person:hover {
  z-index: 1;
  background-color: #eee;
  transform: scale(1.2);
}

.person:hover .person__extra {
  display: block;
}

.person:hover .person__photo > img {
  border-radius: 60px;
}

.person {
  transition: transform 1s;
}
